<template>
    <div class="distributor-index" v-loading="loading">
        <tab-box :top-menu="topMenu" v-if="!loading"></tab-box>
    </div>
</template>

<script>
    import tabBox from "../../components/layout/web/tabBox";
    export default {
        name: "distributor",
        components : {
            tabBox,
        },
        data(){
            return {
                loading:true,
                resultType: -1,
                tableData: {}

            }
        },
		computed:{
			topMenu:{
				get(){
					let list = {
						buttonList: [
						    {
						        text: this.$L('申请提现'),
						        router: 'applyWithdrawal'
						    }
						],
						menuList:[
							{
							    text:this.$L('我的分销商'),
							    router: 'distributorList',
							},
							{
							    text:this.$L('我的会员'),
							    router: 'distributorMember',
							},
							{
							    text:this.$L('分销订单'),
							    router:'distributorOrder',
							    checkRouter:[
							        "distributorUserOrder"
							    ],
							    paramsData: {
							        type: 'all',
							    },
							},
							{
							    text:this.$L('我的佣金'),
							    router:'distributorWithdrawal',
							},
							{
							    text:this.$L('分销设置'),
							    router:'distributorSettings',
							},
                            {
                                text:this.$L('分销商详情'),
                                hide:true,
                                router:'distributorDetails',
                            },
                            {
                                text:this.$L('会员订单'),
                                hide:true,
                                router:'memberOrder',
                            }
						]
					}
					if(this.tableData.sales === 1){
						list.menuList.push({
						    text:this.$L('分销排行'),
						    router:'distributorRanking',
						    paramsData: {
						        type: 'sales'
						    }
						});
					}else if(this.tableData.commission === 1){
						list.menuList.push({
						    text:this.$L('分销排行'),
						    router:'distributorRanking',
						    paramsData: {
						        type: 'commission'
						    }
						});
					}else if(this.tableData.offline === 1){
						list.menuList.push({
						    text:this.$L('分销排行'),
						    router:'distributorRanking',
						    paramsData: {
						        type: 'offline'
						    }
						});
					}
                    list.menuList.push({
                        text:this.$L('页面信息修改'),
                        router:'distributorModify',
                    });
                    list.menuList.push({
                        text:this.$L('模块自定义修改'),
                        hide:true,
                        router:'distributorEditModify',
                    });
					list.menuList.push({
						text:this.$L('申请提现'),
						router:'applyWithdrawal',
						hide:true
                    });
					return list;
				}
			}
		},
        created() {
            this.loading = true;
            if(this.$isMobile()){
                this.$router.push({name:'distributorMain'});
            }else{
                this.review();
                //先进行审核页面的判断
            }
        },
        methods : {
            review() {
                const _self = this;
                _self.loading = true;
                _self.$api('reviewResult', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.resultType = reData.data.resultType;
                        _self.tableData = reData.data;
                        if (_self.resultType !== 1) {
                            _self.$router.push({name:'distributorAudit'});
                        }
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });

            },
        }


    }
</script>

<style lang="scss" scoped>
    .distributor-index{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
    }
</style>